nav {
  padding: 2vh 5vw;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  justify-content: space-between;
  align-items: center;
}

nav div.linkContainer a {
  color: rgb(139, 139, 139);
  font-size: 2.8vmin;
  font-weight: bold;
  text-transform: uppercase;
  text-decoration: none;
  margin-right: 1.5rem;
  transition: all 300ms;
}

nav div.linkContainer a.active {
  color: white;
}

nav div.linkContainer a:hover {
  color: white;
}

div.avatarContainer{
  display: flex;
  align-items: center;
}

div.avatarContainer > span{
  margin-right: .8em;
  color: white;
}

.avatarIcon {
  width: 7vmin;
  height: 7vmin;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 100%;
}

.avatarIcon:hover{
  cursor: pointer;
}

div.loginBox{
  width: auto;
  position: absolute;
  top: 12vmin;
  right: 2vw;
  z-index: 3;
  background-color: #ccc;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: .5em;
  overflow: hidden;
}

nav div.loginBox a,
nav div.loginBox span{
  padding: .5vh 2vw;
  text-align: center;
  width: 100%;
  /* background-color: rgb(163, 7, 7); */
  background-color: rgba(0, 0, 0, 0.589);
  color: white;
  font-size: 2.8vmin;
  font-weight: bold;
  text-transform: uppercase;
  text-decoration: none;
  transition: all 300ms;
}

nav div.loginBox span{
  cursor: pointer
}

nav div.loginBox a:hover,
nav div.loginBox span:hover{
  /* background-color: black; */
  background-color: rgb(163, 7, 7);
  color: white;
}
