header.cities {
  min-height: 80vh;
}
.cityHero {
  width: 100%;
  height: 80vh;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  background-position: center 70%;
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}

.cityHero h1 {
  padding-left: 5vw;
  font-size: 10vh;
  margin-top: 10vh;
  font-family: 'Nunito', monospace;
  font-weight: 500;
  background: #B80000;
  background: -webkit-linear-gradient(to top, #B80000 0%, #5E0A08 82%);
  background: -moz-linear-gradient(to top, #B80000 0%, #5E0A08 82%);
  background: linear-gradient(to top, #B80000 0%, #5E0A08 82%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.gallery {
  /* padding: 3em 10vw; */
  padding: 3vw;
  display: grid;
  grid-template-columns: repeat(2, calc(100% / 2));
  grid-auto-rows: 90vh;
  gap: 0.5em;
}

.gallery a:nth-child(4n-2) div .color {
  background: #ece9e6;
  background: -webkit-linear-gradient(to right, #ffffff, #ece9e6);
  background: linear-gradient(to right, #ffffff, #ece9e6);
}

.gallery a:nth-child(4n-1) div .color {
  background: #000000;
  background: -webkit-linear-gradient(to right, #434343, #000000);
  background: linear-gradient(to right, #434343, #000000);
}

.gallery a:nth-child(4n-1) div .color h1 {
  color: white;
  /* margin-top: 5vh; */
}

.gallery a:nth-child(4n-3) .color div,
.gallery a:nth-child(4n) .color div {
  display: none;
}

.gallery a:nth-child(4n-3) .color h1,
.gallery a:nth-child(4n) .color h1 {
  text-align: center;
  width: 100%;
  padding: 10vh 0;
  /* background-color: rgba(85, 85, 85, 0.219); */
  /* background: linear-gradient(0deg, rgba(85,85,85,0.7) 0%, rgba(0,0,0,0.7) 10%, rgba(16,16,16,0.7) 50%, rgba(0,0,0,0.7) 90%, rgba(85,85,85,0.7) 100%); */
  /*transition: all 1s; */
  background: rgba(173, 169, 150, 0.5); /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    rgba(234, 234, 234, 0.5),
    rgba(219, 219, 219, 0.5),
    rgba(242, 242, 242, 0.5),
    rgba(173, 169, 150, 0.5)
  );
  background: linear-gradient(
    to right,
    rgba(234, 234, 234, 0.5),
    rgba(219, 219, 219, 0.5),
    rgba(242, 242, 242, 0.5),
    rgba(173, 169, 150, 0.5)
  );
}

.searcher {
  border-radius: 30px;
  outline: none;
  padding: 0.5em 1em;
  width: 50%;
  z-index: 2;
  margin: 5vh auto;
  transition: all 300ms;
  border-color: red;
}

.searcher:focus {
  color: rgb(63, 0, 0);
  box-shadow: 2px 2px 2px red;
}

@media (max-width: 992px) {
  .gallery {
    display: grid;
    grid-template-columns: 1fr;
  }
}

.cardPicture {
  width: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  height: 100%;
  grid-column-start: span 1;
  overflow: hidden;
  transition: all 500ms;
}

a.cardLink {
  transition: all 500ms;
  text-decoration: inherit;
}

a.cardLink:hover .cardPicture {
  box-shadow: 3px 3px 5px black;
  transform: scale(1.02);
}

.cardPicture h1 {
  color: black;
  transition: all 500ms;
}

.color {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  flex-wrap: wrap;
}

.color div {
  display: flex;
  justify-content: center;
}

.color div img:first-child {
  background-color: white;
  padding: 10px 10px 50px 10px;
  border: 1px solid #bfbfbf;
  box-shadow: 10px 10px 30px rgba(0, 0, 0, 0.8);
  position: relative;
  transform: rotate(5deg);
  -ms-transform: rotate(5deg);
  -webkit-transform: rotate(5deg);
  -moz-transform: rotate(5deg);
}
.color div img:last-child {
  background-color: white;
  padding: 10px 10px 50px 10px;
  border: 1px solid #bfbfbf;
  box-shadow: 10px 10px 30px rgba(0, 0, 0, 0.8);
  position: relative;
  transform: rotate(-8deg);
  -ms-transform: rotate(-8deg);
  -webkit-transform: rotate(-8deg);
  -moz-transform: rotate(-8deg);
}

a * {
  text-decoration: none !important;
}

.nocity {
  width: 60%;
  margin: 0 auto;
  border-radius: 20px;
  box-shadow: 3px 3px 10px black;
  padding: 0 3vw;
  display: grid;
  grid-template-columns: 1fr 3fr !important;
  grid-template-columns: auto;
  background: #ece9e6;
  background: -webkit-linear-gradient(to right, #ffffff, #ece9e6);
  background: linear-gradient(to right, #ffffff, #ece9e6);
}

.nocity .panda {
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  height: 30vh;
}

.nocity h2{
  font-family: 'Nunito', monospace;
  font-weight: 900;
  background: #B80000;
  background: -webkit-linear-gradient(to top, #B80000 0%, #5E0A08 82%);
  background: -moz-linear-gradient(to top, #B80000 0%, #5E0A08 82%);
  background: linear-gradient(to top, #B80000 0%, #5E0A08 82%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.nocity h3{
  color: rgb(141, 0, 0);
}

.nocity h4{
  color: rgb(116, 0, 0);
}

.nocity div {
  text-align: center;
}

.message {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

@media (max-width: 992px){
  .nocity {
      width: 90%;
  }
}

@media (max-width: 600px) {
  .nocity {
    display: grid;
    grid-template-columns: 1fr !important;
  }
  .nocity .panda{
      height: 20vh !important;
  }
}

